@keyframes fadeInPage {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

body {
  animation: 1.2s forwards fadeInPage;
}

.logo:hover {
  cursor: pointer;
  transition: transform .6s, box-shadow .6s;
  transform: scale(1.1)rotate(10deg);
  box-shadow: 0 0 15px #ffcdb2b3;
}

.nav-li {
  transition: color .3s;
  position: relative;
}

.nav-li:after {
  content: "";
  background-color: #7d4f50;
  width: 0;
  height: 2px;
  transition: width .3s;
  position: absolute;
  bottom: -5px;
  left: 0;
}

.nav-li:hover:after {
  width: 100%;
}

.nav-li:hover {
  color: #7d4f50;
}

.cart-btn:hover, .main-btn:hover, .footer-btn:hover {
  cursor: pointer;
  transition: transform .3s, box-shadow .3s;
  transform: scale(1.08);
  box-shadow: 0 6px 15px #e29578b3;
}

.products-div:hover {
  cursor: pointer;
  transition: box-shadow .4s, transform .4s;
  transform: translateY(-10px)scale(1.05);
  box-shadow: 0 15px 25px #c9b19480;
}

.search-inpt:focus {
  border: 3px solid #7d4f50;
  outline: none;
  box-shadow: 0 0 10px #7d4f50;
}

.clickImage:hover {
  cursor: pointer;
  transition: transform .4s, box-shadow .4s;
  transform: scale(1.06);
  box-shadow: 0 10px 20px #7d4f504d;
}

@keyframes floatUpDown {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-8px);
  }
}

.button-div {
  animation: 3.5s ease-in-out infinite floatUpDown;
}

.footer .social-links a {
  transition: transform .3s;
  display: inline-block;
}

.footer .social-links a:hover {
  cursor: pointer;
  transform: translateY(-6px);
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(-15px);
  }

  60% {
    transform: translateY(-7px);
  }
}

.Beauty-img:hover {
  cursor: pointer;
  animation: 1s bounce;
}

.products-div:hover {
  text-shadow: 2px 5px 8px #f1bf82cc;
  box-shadow: 0 15px 25px #e8631b80;
}

a {
  text-decoration: none;
}
